<template>
  <i :class="classList"></i>
</template>
<script>
export default {
  props: {
    name: String,
    size: Number,
    color: {
      type: String,
      default: "black",
      validator: function (value) {
        return [
          "default",
          "primary",
          "secondary",
          "success",
          "special",
          "info",
          "danger",
          "warning",
          "gray",
        ].includes(value);
      },
    },
    hover: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    classList() {
      const classList = ["ico"];
      if (this.color) classList.push(`ico-${this.color}`);
      if (this.size) classList.push(`ico-size-${this.size}`);
      if (this.name) classList.push(`ico-${this.name}`);
      if (this.hover) classList.push("ico-hover");
      return classList;
    },
  },
};
</script>
<style scoped lang="scss"></style>
